









































import axios from '@/api/axios';
import useLoader from '@4dst-saas/public-utils/dist/composables/use-loader';
import extract from '@4dst-saas/public-utils/dist/extract';
import { computed, defineComponent } from '@vue/composition-api';

const subKey = Symbol('sub');
export const parentKey = Symbol('parent');
const pSubKey = Symbol('pSub');
type CategoryNode = {
  name: string,
  id: string,
  [parentKey]?: CategoryNode,
  [subKey]?: CategoryNode[],
  [pSubKey]?: Promise<CategoryNode[]>
};

type CategoryLeaf = Omit<CategoryNode, typeof subKey | typeof pSubKey | typeof parentKey>;

async function getPaths<Value, Node>(
  values: Value[],
  tree: Node[],
  {
    getChildren,
    isSelected,
    isLeaf,
  }: {
    getChildren: (level: number, node: Node, rootTree: Node[]) => Promise<Node[]> | Node[]
    isLeaf: (node: Node) => boolean,
    isSelected: (value: Value, node: Node) => boolean
  },
) {
  const result = [];
  const rootTree = tree;
  for (let i = 0; i < values.length; i++) {
    const value = values[i];
    const node = tree.find((_node) => {
      return isSelected(value, _node);
    });
    if (node) {
      result.push(node);
      if (isLeaf(node)) {
        break;
      } else {
        tree = await getChildren(i + 1, node, rootTree);
      }
    } else {
      break;
    }
  }
  return result;
}
function _isLeaf(node: CategoryNode): node is CategoryLeaf {
  return !(pSubKey in node);
}
const allItem = { name: '全部', id: null };
async function _getChildren(level: number, node: CategoryNode = {} as CategoryNode) {
  const lastLevel = level === 1;
  node[pSubKey] ??= (async () => {
    const list = (await axios.get<CategoryNode[]>(
      '/show/category/list', { params: { parentId: node.id } },
    )).data.map(item => {
      return { ...item, ...(lastLevel ? {} : { [pSubKey]: undefined }), [parentKey]: node } as CategoryNode;
    });
    list.unshift({ ...allItem, [parentKey]: node });
    return list;
  })();
  node[subKey] = await node[pSubKey];
  return node[subKey]!;
}

function _isSelected<V extends string>(value: V, node: CategoryNode): node is (CategoryNode & { id: V }) {
  return node.id === value;
}

async function getRoot() {
  const root = (await axios.get<CategoryNode[]>(
    '/show/category/list', { params: { parentId: -1 } },
  )).data[0];
  root[pSubKey] = _getChildren(0, root);
  root[subKey] = await root[pSubKey];
  return root;
}

const pRoot = getRoot();

export default defineComponent({
  props: {
    value: {
      type: Array as () => string[],
      required: true,
    },
  },
  setup(props, { emit }) {
    const v = computed(() => {
      const _v = [...props.value];
      if (_v.length < 2 && _v.length && _v[_v.length - 1]) {
        _v.push(null);
      }
      return _v;
    });
    const paths = useLoader(async ({ value }) => {
      const root = await pRoot;
      return getPaths(value, root[subKey]!, {
        isLeaf: _isLeaf,
        isSelected: _isSelected,
        getChildren: _getChildren,
      });
    }, { value: v }, {
      initialValue: [] as CategoryNode[],
    });
    const lists = computed(() => {
      return paths.value.map(item => item[parentKey]![subKey]!);
    });
    return {
      v,
      lists,
      paths,
      async pick(item: CategoryNode) {
        const root = await pRoot;
        const _paths: CategoryNode[] = [];
        let _item: CategoryNode | undefined = item;
        while (_item && _item !== root) {
          _paths.push(_item);
          _item = _item[parentKey];
        }
        _paths.reverse();
        emit('update:value', _paths.map(__item => __item.id), _paths);
      },
    };
  },

});
